import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class ReportMakerService {

  constructor(
    private http: HttpClient
  ) { }


  generateReportUrl: string = 'http://10.204.135.171/generatereport';
  body: {} = {}
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    })
  }

  generateReport(type:string, startDate:string, endDate:string): Observable<any> {
    this.body = {
      'type': type.toLowerCase(),
      'start': startDate,
      'end': endDate
    }
    console.log(this.body, this.httpOptions);
    return this.http.post<any>(this.generateReportUrl, this.body)
        .pipe(
          map((response:any)=>{
          console.log(response);
          return response;
        }),
        catchError(err => of([]))
        );
  }
}
