import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-data-source',
  templateUrl: './data-source.component.html',
  styleUrls: ['./data-source.component.scss']
})
export class DataSourceComponent implements OnInit, OnDestroy {
  constructor(
    private http: HttpClient
  ) { }
  dataSourceButton: string = 'SQL';
  databaseList: string[] = ['QString','QPay'];
  qStringTables: string[] = ['Company', 'Employee', 'Employment Detail'];
  qPayTables: string[] = ['QPay Data Export Data', 'Attendance Detail', 'Invoice Detail'];

  selectedDatabase: string = this.databaseList[0];
  displayedTables: string[] = this.qStringTables;

  dtOptions1: DataTables.Settings = {};
  table1:any = [];
  dtTrigger1: Subject<unknown> = new Subject();

  dtOptions2: DataTables.Settings = {};
  table2:any = [];
  dtTrigger2: Subject<unknown> = new Subject();

  dtOptions3: DataTables.Settings = {};
  table3:any = [];
  dtTrigger3: Subject<unknown> = new Subject();


  objectKeys = Object.keys;

  ngOnInit(): void {
    this.populateTables();
  }
  ngOnDestroy(): void {
    this.dtTrigger1.unsubscribe();
    this.dtTrigger2.unsubscribe();
    this.dtTrigger3.unsubscribe();
  }
  populateTables(){
    this.dtOptions1 = {
      pagingType: 'full_numbers',
    };
    this.dtOptions2 = {
      pagingType: 'full_numbers',
    };
    this.dtOptions3 = {
      pagingType: 'full_numbers',
    };
    this.http.get(`/assets/${this.displayedTables[0]}.json`)
      .subscribe(table1 => {
        console.log(table1);
        this.table1 = table1;
        // Calling the DT trigger to manually render the table
        this.dtTrigger1.next();
      });
    this.http.get(`/assets/${this.displayedTables[1]}.json`)
      .subscribe(table2 => {
        console.log(table2);
        this.table2 = table2;
        // Calling the DT trigger to manually render the table
        this.dtTrigger2.next();
      });
    this.http.get(`/assets/${this.displayedTables[2]}.json`)
      .subscribe(table3 => {
        console.log(table3);
        this.table3 = table3;
        // Calling the DT trigger to manually render the table
        this.dtTrigger3.next();
      });
  }
  toggleDataSource(){
    this.dataSourceButton == 'SQL' ? this.dataSourceButton = 'SAP' : this.dataSourceButton = 'SQL';
  }
  selectDatabase(value){
    this.selectedDatabase = value;
    if(this.selectedDatabase == this.databaseList[0]){
      this.displayedTables = this.qStringTables;
    }else if(this.selectedDatabase == this.databaseList[1]){
      this.displayedTables = this.qPayTables;

    }
    this.populateTables();
  }

}
