import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { DataTableDirective } from 'angular-datatables';

@Component({
  selector: 'app-data-stream',
  templateUrl: './data-stream.component.html',
  styleUrls: ['./data-stream.component.scss']
})
export class DataStreamComponent implements OnInit, OnDestroy {
  constructor(
    private http:HttpClient
  ) { }

  databaseList: string[] = ['QString','QPay'];
  qStringTables: string[] = ['Company', 'Employee', 'Employment Detail', 'ClientInActive', 'Segment'];
  qPayTables: string[] = ['Attendance Detail'];
  selectedDataSource: string = this.qStringTables[0];
  showTable: boolean = true;

  dtOptions: DataTables.Settings = {};
  table:any = [];
  dtTrigger: Subject<any> = new Subject();
  subscription: Subscription;
  isFirst: boolean = true;
  @ViewChild(DataTableDirective, {static: false})
  dtElement: DataTableDirective;

  objectKeys = Object.keys;

  ngOnInit(): void {
    this.populateTables(this.selectedDataSource);
  }
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.dtTrigger.unsubscribe();
  }
  populateTables(dataSource: string){
    this.dtOptions = {
      destroy: true,
      pagingType: 'full_numbers',
    };
    
    this.subscription = this.http.get(`/assets/${dataSource}.json`)
      .subscribe(table => {
        console.log(table);
        this.table = table;
        // Calling the DT trigger to manually render the table
        if(this.isFirst){
          this.isFirst = false;
          this.dtTrigger.next();
        }else{
          this.rerender();
        }
      });
  }
  rerender(): void {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
        dtInstance.destroy();
        this.dtTrigger.next();
    });
}
  selectDataSource(value){
    this.selectedDataSource = value;
    this.showTable = false;
    let that =this;
    setTimeout(function(){
      that.showTable = true;
      that.populateTables(value); 
    },100)
    //this.showTable = true;
    
  }
}
