import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DataSourceComponent } from './data-source/data-source.component';
import { DataStreamComponent } from './data-stream/data-stream.component';
import { DataMartComponent } from './data-mart/data-mart.component';
import { DataAnalyticsComponent } from './data-analytics/data-analytics.component';
import { ReportViewerComponent } from './report-viewer/report-viewer.component';


const routes: Routes = [
  {
    path: 'data-source',
    component: DataSourceComponent
   },
   {
    path: 'data-stream',
    component: DataStreamComponent
   },
   {
    path: 'data-mart',
    component: DataMartComponent
   },
   {
    path: 'data-analytics',
    component: DataAnalyticsComponent
   },
   {
    path: 'report-viewer',
    component: ReportViewerComponent
   },
   {
    path: '',
    redirectTo: '/data-source',
    pathMatch: 'full'
  },
  {
    path: '**',
    redirectTo: 'data-source'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
