import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-data-mart',
  templateUrl: './data-mart.component.html',
  styleUrls: ['./data-mart.component.scss']
})
export class DataMartComponent implements OnInit {
  constructor(
    private http: HttpClient
  ) { }

  datamarts: string[] = ['Employee', 'Finance', 'Company'];
  selectedDatamart: string = this.datamarts[0];
  showTable: boolean = true;

  dtOptions: DataTables.Settings = {};
  table: any = [];
  dtTrigger: Subject<unknown> = new Subject();

  objectKeys = Object.keys;

  ngOnInit(): void {
    this.populateTables(this.selectedDatamart);
  }
  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }
  populateTables(dataSource: string) {
    this.dtOptions = {
      pagingType: 'full_numbers',
    };

    this.http.get(`/assets/${dataSource}.json`)
      .subscribe(table => {
        console.log(table);
        this.table = table;
        // Calling the DT trigger to manually render the table
        this.dtTrigger.next();
      });
  }
  selectDatamart(value) {
    this.selectedDatamart = value;
    this.showTable = false;
    setTimeout(function () {
      this.showTable = true;
    }, 100)
    //this.showTable = true;
    //this.populateTables(value);
  }
}
